/* -------------------------------------------------------------------------------------------------
  # Galleries
------------------------------------------------------------------------------------------------- */
.gallery {
  display: flex;
  flex-wrap: wrap;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  flex: 0 0 100%;
  max-width: 100%;

  .gallery-columns-2 & {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .gallery-columns-3 & {
    flex: 0 0 33.33%;
    max-width: 33.33%;
  }

  .gallery-columns-4 & {
    flex: 0 0 25%;
    max-width: 25%;
  }

  .gallery-columns-5 & {
    flex: 0 0 20%;
    max-width: 20%;
  }

  .gallery-columns-6 & {
    flex: 0 0 16.66%;
    max-width: 16.66%;
  }

  .gallery-columns-7 & {
    flex: 0 0 14.28%;
    max-width: 14.28%;
  }

  .gallery-columns-8 & {
    flex: 0 0 12.5%;
    max-width: 12.5%;
  }

  .gallery-columns-9 & {
    flex: 0 0 11.11%;
    max-width: 11.11%;
  }
}

.gallery-caption {
  display: block;
}
