/* -------------------------------------------------------------------------------------------------
  # WordPress Specific Styles
------------------------------------------------------------------------------------------------- */
// Accessibility
// -----------------------------------------------------------------------------
.screen-reader-text {
  position: absolute !important;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;

  &:focus {
    z-index: 100000; // Stay above WP tool bar
    top: 5px;
    left: 5px;
    display: block;
    width: auto;
    height: auto;
    padding: rem(14) rem(24);
    border-radius: 3px;
    background-color: $color_off-white;
    box-shadow: 0 0 2px 2px rgba($color_black, 0.6);
    clip: auto !important;
    font-size: rem(12);
    color: $color_blue;
    font-weight: 700;
    line-height: normal;
    text-decoration: none;
  }
}

// Do not show the outline on the skip link target
#content[tabindex="-1"]:focus {
  outline: 0;
}

// Alignments
// -----------------------------------------------------------------------------
.alignleft {
  display: inline;
  float: left;
  margin-right: 1rem;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1rem;
}

.aligncenter {
  @include center-block;
  clear: both;
}
