/* -------------------------------------------------------------------------------------------------
  # WordPress Specific Styles
------------------------------------------------------------------------------------------------- */
.screen-reader-text {
  position: absolute !important;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
}

.screen-reader-text:focus {
  z-index: 100000;
  top: 5px;
  left: 5px;
  display: block;
  width: auto;
  height: auto;
  padding: 0.875rem 1.5rem;
  border-radius: 3px;
  background-color: #f1f1f1;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  font-size: 0.75rem;
  color: #21759b;
  font-weight: 700;
  line-height: normal;
  text-decoration: none;
}

#content[tabindex="-1"]:focus {
  outline: 0;
}

.alignleft {
