}

/* -------------------------------------------------------------------------------------------------
  # Tables
------------------------------------------------------------------------------------------------- */
table {
  margin-bottom: 1rem;
  width: 100%;
  text-align: left;
}

/* -------------------------------------------------------------------------------------------------
  # Widgets
------------------------------------------------------------------------------------------------- */
.widget {
  margin-bottom: 1rem;
}

.widget select {
