}

/* -------------------------------------------------------------------------------------------------
  # Primary Menu
------------------------------------------------------------------------------------------------- */
.site_navigation {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: white;
}

.menu li {
  flex: 0 1 auto;
  margin: 0;
  padding: 0;
  position: relative;
}

@media only screen and (min-width: 64em) {
  .menu li:hover > .sub-menu {
    display: block;
  }
}

.menu a {
  text-decoration: none;
  white-space: nowrap;
  padding: 0.25rem;
}

.sub-menu {
  display: none;
  position: absolute;
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: white;
}

.sub-menu a {
  display: block;
}

.sub-menu .sub-menu {
  position: absolute;
  top: 0;
  left: 100%;
}

/* -------------------------------------------------------------------------------------------------
  # Post and Comment Navigation
------------------------------------------------------------------------------------------------- */
.site_main .comments_navigation, .site_main
.posts-navigation, .site_main
.post-navigation {
  margin-bottom: 1rem;
  overflow: hidden;
}

.comments_navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%;
}

.comments_navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%;
}

/* -------------------------------------------------------------------------------------------------
  # Posts Base
------------------------------------------------------------------------------------------------- */
.sticky {
  display: block;
}

.hentry {
  margin-bottom: 1rem;
}

.byline,
.updated:not(.published) {
  display: none;
}

.single .byline,
.group-blog .byline {
  display: inline;
}

.page_content,
.post_content,
.post_summary {
  margin-top: 1rem;
}

.page_links {
  clear: both;
  margin-bottom: 1rem;
}

.category-links,
.tag-links,
.comment-link {
  display: block;
}
