// Custom mixins
// -----------------------------------------------------------------------------
// Center block
@mixin center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

// Clearfix
@mixin clearfix {
  content: "";
  display: table;
  table-layout: fixed;
}

// Clear after
@mixin clearfix-after {
  clear: both;
}
